import React, { Component } from 'react'
import '../Faq/Faq.css'
import $ from 'jquery'
import data from '../Faq/FaqData'

class Faq extends Component {
  constructor(props) {
    super(props)
    this.state = {
      display1: 'none',
      display2: 'block',
    }
  }

  

  showhide(event, id) {
    event.preventDefault()
    var x = document.getElementById(id)
    if (x.style.display == 'none' || x.style.display == '') {
      x.style.display = 'block'
    } else {
      x.style.display = 'none'
    }
  }
  render() {
    return (
      <>
        <div className="section faqq will-animate">
          <div className="section-content faq-content">
            <div className="faq-container">
              <div className="faq-title">
                <div className="default-title revealator-slideleft revealator-once revealator-within">
                  <h2>
                    <span>FAQ</span>
                  </h2>
                </div>
              </div>
              
              {data.map((value, i) => {
                return (
                  <div
                    className={i < 9 ? 'accordion-wrap' : 'accordion-wrap-2'}
                    key={i}
                  >
                    <div className="accordion revealator-slideright revealator-once revealator-within">
                      <div className="accordion-heading">
                        <p>{value.p1}</p>
                        <span>
                          <i
                            class="fa fa-chevron-down"
                            aria-hidden="true"
                            onClick={(e) =>
                              this.showhide(e, 'accordion-content' + i)
                            }
                          ></i>
                        </span>
                      </div>

                      <div
                        className="accordion-content"
                        id={`accordion-content${i}`}
                      >
                        <p>{value.p2}</p>
                        <br />
                        <p>{value.p3}</p>
                      </div>
                    </div>
                  </div>
                )
              })}

            </div>
          </div>
        </div>
      </>
    )
  }
}

export default Faq
