import React, { Component } from 'react'
import $ from 'jquery'
import '../Resgister/Register.css'
import ApiLink from '../Domain/domain'
import validator from 'validator'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


class Register extends Component {
  constructor(props) {
    super(props)
    this.state = {
      name: '',
      email: '',
      address: '',
      login_form_data: {
        id: null
      },
    }
  }

  signup_form_data = (event) => {
    var name = event.target.name
    var value = event.target.value
    
    this.setState(({ signup_form_data }) => ({
      signup_form_data: { ...signup_form_data, [name]: value },
    }))
  }


  get_form_data = (event) => {
    var name = event.target.name
    var value = event.target.value
    this.setState(({ login_form_data }) => ({
      login_form_data: { ...login_form_data, [name]: value },
    }))
  }


  call_state = (event) => {
    event.preventDefault()
    // LOGIN API CALL
    fetch(ApiLink + 'login', {
      method: 'POST',
      headers: new Headers({
        'Content-type': 'application/json',
        Accept: 'application/json',
      }),
      body: JSON.stringify(this.state.login_form_data),
    })
      .then((response) => response.json())
      .then((res) => {
        if (res.code == 200 && res.result.length > 0) {
          this.props.history.push('/dashboard')
        } else {
          toast.error('Invalid Credentials')
        }
      })
      .catch((err) => console.log('error :::::', err))
  }

  // api register

  call_state_register = async (event) => {
    event.preventDefault()
    if (validator.isEmail(this.state.signup_form_data.email)) {
      // Register API Call
      fetch(ApiLink + 'register', {
        method: 'POST',
        headers: new Headers({
          'Content-type': 'application/json',
        }),
        body: JSON.stringify(this.state.signup_form_data),
      })
        .then((response) => response.json())
        .then((res) => {
          if (res.code == 200) {
            toast.success('Successfully signup, Please login with id > ' + res.result.id)
          } else toast.error(res.message)
        })
        .catch((err) => toast.error('All Fields are compulsory'))
      // End of Register Api call
    } else {
      toast.error('Please put valid Email Id')
    }
  }

  componentDidMount() {
    $('.log-in').click(function () {
      $('.signIn').addClass('active-dx')
      $('.signUp').addClass('inactive-sx')
      $('.signUp').removeClass('active-sx')
      $('.signIn').removeClass('inactive-dx')
    })

    $('.sign-up').click(function () {
      $('.signUps').addClass('active-dx')
      $('.signUp').addClass('inactive-sx')
      $('.signUp').removeClass('active-sx')
      $('.signUps').removeClass('inactive-dx')
    })

    $('.back').click(function () {
      $('.signUp').addClass('active-sx')
      $('.signIn').addClass('inactive-dx')
      $('.signIn').removeClass('active-dx')
      $('.signUp').removeClass('inactive-sx')
      $('.signUps').removeClass('active-dx')
    })
  }

  render() {
    return (
      <>
        <ToastContainer />
        <div className="container">
          <form className="signUp">
            <h3>Create Your Account</h3>
            <div className="signup-heading" style={{ marginTop: '10rem' }}>
              <h3>Join by only</h3>
              <h3>1 TRX</h3>
            </div>
            <button className="form-btn sx log-in" type="button">
              Log In
            </button>
            <button className="form-btn dx sign-up" type="button">
              Sign Up
            </button>
          </form>

          <form className="signIn">
            <h3>
              Welcome <br /> Back
            </h3>
            <input
              type="number"
              placeholder="Insert ID"
              autocomplete="off"
              name="id"
              onChange={this.get_form_data}
              required
            />
            <button className="form-btn sx back" type="button">
              Back
            </button>
              <button className="form-btn dx" type="submit" 
              onClick={(e) => this.call_state(e)}>
                Log In
              </button>
          </form>

          <form className="signUps">
            <h3>
              Welcomes <br /> Back
            </h3>
            <input
              type="text"
              placeholder="Insert Name"
              name="name"
              autocomplete="off"
              required
              onChange={this.signup_form_data}
            />

            <input
              type="text"
              placeholder="Insert Wallet Address"
              name="address"
              autocomplete="off"
              required
              onChange={this.signup_form_data}
            />

            <input
              type="email"
              placeholder="Insert Email"
              autocomplete="off"
              name="email"
              required
              onChange={this.signup_form_data}
            />
            <button className="form-btn sx back" type="button">
              Back
            </button>
              <button className="form-btn dx" type="submit" 
                          onClick={(e) => this.call_state_register(e)}>
                Log In
              </button>
          </form>
        </div>
      </>
    )
  }
}

export default Register
