import React, { Component } from 'react'
import '../Carousel/Carousel.css'
import { NavLink } from 'react-router-dom'

class Carousel extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  render() {
    return (
      <>
        {/* <div className="main-heading"></div> */}
        <div className="col-xxl-12 col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="main-logo">
            <img src="./img/logo.png" alt="" srcset="" style={{height:'25rem'}}/>
          </div>
        </div>
        <div className="col-xxl-12 col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <p className="first-heading">THE WORLD’S TOP</p>
        </div>

        <div className="col-xxl-12 col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <p className="sec-heading">100% DECENTRALIZED</p>
        </div>

        <div className="col-xxl-12 col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <p className="sec-heading">MATRIX PLATFORM</p>
        </div>

        <div className="col-xxl-4 col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12">
          <div className="main-btn">
            <NavLink to="/register">
            <button type="button" class="btn btn-primary btn-main def-btn" >
              REGISTRATION
            </button>
            </NavLink>
          </div>
        </div>

        <div className="col-xxl-4 col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12">
          <div className="main-btn">
            <button type="button" class="btn btn-primary btn-main def-btn">
              MARKETING VIDEO
            </button>
          </div>
        </div>

        <div className="col-xxl-4 col-xl-4 col-lg-4 col-md-12 col-sm-12 col-xs-12">
          <div className="main-btn">
            <NavLink to="/register">
            <button type="button" class="btn btn-primary btn-main def-btn">
              LOGIN
            </button>
            </NavLink>
          </div>
        </div>
      </>
    )
  }
}

export default Carousel

